@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans:wght@300;400;500;600;700&family=Poppins:wght@300;400;500;600&display=swap);
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root{
    --color-bg:#1f1f38;
    --color-bg-variant:#2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-variant:rgba(77, 181, 255, 0.4);
    --color-white:#fff;
    --color-light:rgba(255, 255, 255, 0.6);

    --transition:all 400ms ease ;

    --container-width-lg:75%;
    --container-width-md: 86%;
    --container-width-sm:90%;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(/static/media/bg.90683044.jpg);
    background-size:cover;
}
/* ================ GENERAL STYLES ================ */
.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}
h1,
h2,
h3,
h4,
h5{
    font-weight : 500;
}
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
}
section > h2, section >h5{
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}
section>h2{
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}
a{
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition)
}
a:hover{
    color: #fff;
    color: var(--color-white);
}
.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.7rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary );
    transition: all 400ms ease;
    transition: var(--transition);
}
.btn:hover{
    background: #fff;
    background: var(--color-white);
    color:#1f1f38;
    color:var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}
img{
    display: block;
    width: 100%;
    object-fit: cover
}


/* ============== MEDIA QUERIES ( MEDIUM DEVICES ) =============== */

@media screen and (max-width: 1024px) {
    .container{
        width: 86%;
        width: var(--container-width-md) ;
    }
    section{
        margin-top: 6rem;
        
    }
}

/* ============== MEDIA QUERIES ( SMALL DEVICES ) =============== */

@media screen and (max-width: 600px) {
    .container{
        width: 90%;
        width: var(--container-width-sm) ;
    }
    section >h2{
        margin-top: 2rem;
    }
}
@media screen and (max-width: 600px) {
    .max-width{
        padding: 0px 80px;
    }
}
.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent, var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

.about__me-img{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-img:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}
.about__card{
    background: var(--color-primary-variant); 
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about__card h5{
    font-size: 1rem;
}
.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about__content p{
    margin: 2rem 0 2.6rem;
}

/* ============== MEDIA QUERIES ( MEDIUM DEVICES ) =============== */

@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    } 


}
/* ============== MEDIA QUERIES ( SMALL DEVICES ) =============== */

@media screen and (max-width: 600px){
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }
    .about__content p{
        margin: 1.5rem 0;
    }
    
}
.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12rem;
    gap: 12rem;
}
.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
.contact__option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}
.contact__option:hover{
    background-color: transparent;
    border-color: var(--color-primary-variant);
}
.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/******** Form *********/

form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}


/* ============== MEDIA QUERIES ( MEDIUM DEVICES ) =============== */
@media screen and (max-width: 1024px) {
    .container.contact__container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* ============== MEDIA QUERIES ( SMALL DEVICES ) =============== */
@media screen and (max-width: 600px){
    .container.contact__container{
        width: var(--container-width-sm);
    }
}
.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}
.experience__container >div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.experience__container >div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.experience__container >div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}
.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.experience__details-icon{
    margin-top:5.5px;
    color: var(--color-primary);
}

/* ============== MEDIA QUERIES ( MEDIUM DEVICES ) =============== */

@media screen and (max-width: 1024px) {
    .experience__container{
        grid-template-columns: 1fr;
    }
    
    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience__content{
        padding: 1rem;
    }
}
/* ============== MEDIA QUERIES ( SMALL DEVICES ) =============== */

@media screen and (max-width: 600px){
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience__container >div{
        width: 100%;
        padding: 2rem 1rem;
    }
}
footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    color: var(--color-bg) ;
}
footer logo{
    font-size: 2rem;
    font-weight: 500;
    margin: 2rem;
    display: inline-block;
}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer__socials{
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
    font-size: 1.2rem;
}

/* ============== MEDIA QUERIES ( SMALL DEVICES ) =============== */

@media screen and (max-width: 600px){
    .permalinks{    
        flex-direction: column;
        grid-gap:1.5rem;
        gap:1.5rem;
    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
}
header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
    margin-top: 5;
}

/************** CTA ***************/

.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}
/**************  HEADER SOCIALS ***************/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.header__socials .line{
    width: 3px;
    height: 40px;
    background-color: var(--color-primary);
    border-radius: 10px;
}
.header__sociials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
/************* ME ***************/
.me{
    background: linear-gradient( var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);    /*half width*/
    margin-top: 4rem;   
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem ;
}
/**************** SCROLL DOWN ******************/

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ============== MEDIA QUERIES ( MEDIUM DEVICES ) =============== */

@media screen and (max-width: 1024px) {
    header{
        height: 68vh; 
    }
}
/* ============== MEDIA QUERIES ( SMALL DEVICES ) =============== */

@media screen and (max-width: 600px) {
    header{
        height: 100vh;
        padding-top: 3rem;
    }
    .header__socials, .scroll__down{
        display: none;
    }
}

nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem ;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 2.5rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover{
    background:rgba(0, 0, 0, 0.3);
}
nav a.active{
    background: var(--color-primary);
    color: var(--color-bg); 
}
.blog .max-width{
    padding: 0px 20px;
}
.blog .title {
    color: #ff014f;
    letter-spacing: 1px;
    display: block;
    line-height: 14px;
    text-align: center;
    margin-top: 50px;
  }
  .blog .sub-title {
    text-align: center;
    font-size: 40px;
    font-weight: 700;
    line-height: 1.2;
    color: #f8f9fa;
    margin-top: 15px;
    margin-bottom: 60px;
  }
  
  .blog .container-blog {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    gap: 20px;
  }
  .blog .blog-content {
    position: relative;
    box-shadow: 0px 0px 30px 9px rgba(0, 0, 0, 0.4);
    border-radius: 20px;
    padding-top: 100%;
    overflow: hidden;
  }
  .blog .blog-content img {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
    width: 100%;
    transition: 5s ease-in-out;
  }
  .blog .blog-content img:hover{
      top: 100%;
      -webkit-transform: translateY(-100%);
              transform: translateY(-100%);
  }
  .blog .blog-content .title-blog {
    color: #ff014f;
    font-weight: 700;
    font-size: 12px;
    letter-spacing: 1px;
    text-transform: uppercase;
    display: inline-block;
    position: relative;
    padding: 4px;
    margin-top: 10px;
  }
  .blog .blog-content .desc {
    font-weight: 600;
    font-size: 23px;
    line-height: 34px;
    display: inline-block;
    color: #f8f9fa;
  }
  .blog .blog-content .desc:hover {
    color: #ff014f;
    cursor: pointer;
  }
  .blog .blog-content:hover {
    background-color: #1b1d21;
    transition: 0.5s ease-in-out;
    cursor: pointer;
  }
  
  /* responsive */
  @media (max-width: 500px) {
    .blog .sub-title {
      text-align: center;
      font-size: 22px;
      margin-bottom: 30px;
    }
    .blog .container-blog {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: repeat(3, 1fr);
      grid-gap: 10px;
      gap: 10px;
    }
  
    .blog .blog-content .desc {
      font-weight: 600;
      font-size: 20px;
      line-height: 34px;
      display: inline-block;
      color: #f8f9fa;
      padding: 10px
    }
  }
.services__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 3rem;
    gap: 3rem;
}
.service{
    background:var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition: var(--transition);
}
.service:hover{
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}
.service__head{
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1)
}
.service__head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}
.service__list{
    padding: 2rem;
}
.service__list li{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
}
.service__list-icon{
    color: var(--color-primary);
    margin-top: 2px;
}
.service__list p{
    font-size: 0.9rem;
}

/* ============== MEDIA QUERIES ( MEDIUM DEVICES ) =============== */

@media screen and (max-width: 1024px) {
    .services__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
    .service{
        height: auto;
    }
}
/* ============== MEDIA QUERIES ( SMALL DEVICES ) =============== */

@media screen and (max-width: 600px){
    .services__container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}
